import React from "react";

import subs from "../../images/resources/subs.jpg";
import "./style.css";

const Subscribe = (props) => {
  const submitHandler = (e) => {
    e.preventDefault();
  };
  return (
    <section className={`layout3 subs-sec sub-sec-2 ${props.subclass}`}>
      <div className="container">
        <div className="subscribe-sec">
          <div className="row">
            <div className="col-lg-6">
              <div className="subs-img">
                <img src={subs} alt="" />
              </div>
            </div>
            <div className="col-lg-6">
              <div className="subs-text">
                <div className="sec-title">
                  <h2>Subscribe Our Newsletter</h2>
                </div>
                <p>Keep up with our latest news and promotions </p>
                <form onSubmit={submitHandler}>
                  <input
                    type="email"
                    name="email"
                    placeholder="Enter Your Email"
                  />
                  <button type="submit">Subscribe Now</button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
export default Subscribe;
