import React from "react";
import SectionTite from "../sectiontitle";
import hero1 from "../../images/resources/hero1.png";
import icon7 from "../../images/Ficon/7.png";
import icon8 from "../../images/Ficon/8.png";

import "./style.css";

const TipBusiness = () => {
  return (
    <div className="tips-business-sec">
      <div className="container">
        <div className="row">
          <div className="col-lg-6">
            <div className="tips-col">
              <SectionTite sectitle={"Why Design Insights?"} />
              <p>
                Design Insights are some of the most powerful things in the
                galaxy. Theyre the flashes of deep understanding that are only
                unearthed through careful observation, accurate data, and years
                of experience. And they can make the difference between a
                billion dollar product and one that fails miserably.
              </p>
              <div className="tps-col">
                <div className="tps-row">
                  <div className="tp-icon">
                    <img src={icon7} alt="" />
                  </div>
                  <div className="tp-info">
                    <h3>Hunches</h3>
                    <p>
                      Design Insights start with a Hunch, an assertion based on
                      the current understanding of a problem. A Hunch is shaped
                      by previous experience, or even previous Design Insights,
                      and provides something to test and gather feedback from.
                    </p>
                  </div>
                </div>
                <div className="tps-row">
                  <div className="tp-icon">
                    <img src={icon8} alt="" />
                  </div>
                  <div className="tp-info">
                    <h3>Observations </h3>
                    <p>
                      To prove or disprove Hunches, we gather information, often
                      through testing. As we collect more data, we make
                      Observations based on the patterns we see.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-6">
            <div className="vector-sd">
              <img src={hero1} alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default TipBusiness;
