import React from "react";
import about from "../../images/resources/about.jpg";
import { Link } from "react-router-dom";

import "./style.css";

const Mareketingarea = (props) => {
  return (
    <section className={`marketing-sol-sec ${props.nobg}`}>
      <div className="container">
        <div className="row">
          <div className="col-lg-6">
            <div className="vector-img">
              <img src={about} alt="" />
            </div>
          </div>
          <div className="col-lg-6">
            <div className="sol-col">
              <div className="sec-title">
                <h2>BlackGyri Labs!</h2>
              </div>
              <p>
                In the simplest terms, BlackGyri is a product design company. We
                help companies design incredible digital products (things like
                desktop software, mobile apps, etc), websites, and integrated
                services.
              </p>
              <p>
                But that definition barely scratches the surface of what we do
                here. Our greatest strength lies in our ability to surface
                design insights, those rare and precious revelations of deep
                understanding that catapult projects forward and drive teams
                toward radical innovation. Theyre the difference between good
                ideas and groundbreaking ones and can make the difference
                between a product that falters and one that generates billions
                in revenue.
              </p>
              <p>
                Our work doesnt stop with the designs themselves either. We
                transform entire organizations as well. Through our training, we
                help companies not just create better work, but work together
                better. We teach multiple teams to speak the same language,
                build empathy with each other, and give them the tools to
                communicate their ideas in ways that build unstoppable momentum.
                Working with BlackGyri doesnt just propel your project forward,
                it can help level up your whole organization.
              </p>
              <p>
                None of this would be possible without our dynamic and
                empowering culture. Its built on a set of shared values that
                shapes everything we do, both as individuals and as a whole. We
                sincerely believe that design has near limitless potential to
                change our world for the better, and this common belief has
                drawn together our creative, curious and eccentric group of
                designers, researchers and engineers.
              </p>
              {/* <Link to="/" title="" className="lnk-default">
                About Company{" "}
                <span className="next-btn">
                  <i className="fa fa-long-arrow-right"></i>
                </span>
              </Link> */}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
export default Mareketingarea;
