import React from "react";
import "./style.css";
import { Link } from "react-router-dom";

const Hero = (props) => {
  return (
    <div className="banner-section">
      <div className="container">
        <div className="banner-text">
          <h2>Data + Design</h2>
          <p>
            Greetings, Earthling  Were ZURB, a team of designers, researchers
            and nerds that help companies design better products, websites and
            services by using user data.
          </p>
          <Link to="/contact" title="" className="btn-default">
            Get Started
          </Link>
        </div>
        <div className="vectorr_shape">
          <img src={props.heroimg} alt="" />
        </div>
        <div className="clearfix"></div>
      </div>
    </div>
  );
};

export default Hero;
