import React from "react";
import { Link } from "react-router-dom";
import Logo from "../../images/ft-logo.png";
import App1 from "../../images/btn1.png";
import App2 from "../../images/btn2.png";

import "./style.css";

const FooterT = () => {
  return (
    <div className="top-footer no-bg">
      <div className="container">
        <div className="footer-content">
          <div className="row">
            <div className="col-lg-3 col-md-6 col-sm-6">
              <div className="widget widget-logo">
                <img src={Logo} alt="logo" />
                <p>Got a project you're excited about and think we can help?</p>
              </div>
            </div>
            <div className="col-lg-2 col-md-6 col-sm-6">
              <div className="widget widget-links">
                <h3 className="widget-title">QUICK LINKS</h3>
                <ul className="lnks-list">
                  <li>
                    <Link to="/about" title="">
                      About
                    </Link>
                  </li>
                  <li>
                    <Link to="/contact" title="">
                      Contact
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-lg-2 col-md-4 col-sm-6">
              <div className="widget widget-contact">
                <h3 className="widget-title">Contact Us</h3>
                <ul className="lnks-list">
                  <li>+234 813 616 5990</li>
                  <li>
                    <Link to="/" title="">
                      www.blackgyrilabs.com
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-lg-2 col-md-3 col-sm-6">
              <div className="widget widget-about">
                <h3 className="widget-title">ABOUT</h3>
                <ul className="lnks-list">
                  <li>
                    <Link to="/" title="">
                      About BlackGyri Labs
                    </Link>
                  </li>
                  <li>
                    <Link to="/" title="">
                      Team
                    </Link>
                  </li>
                  <li>
                    <Link to="/" title="">
                      Testimonials
                    </Link>
                  </li>
                  <li>
                    <Link to="/" title="">
                      Blog
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-lg-3 col-md-5">
              <div className="widget widget-app">
                <h3>Follow Us Now</h3>
                <ul className="social-links">
                  <li>
                    <Link to="/" title="">
                      <i className="fa fa-facebook"></i>
                    </Link>
                  </li>
                  <li>
                    <Link to="/" title="">
                      <i className="fa fa-twitter"></i>
                    </Link>
                  </li>
                  <li>
                    <Link to="/" title="">
                      <i className="fa fa-skype"></i>
                    </Link>
                  </li>
                  <li>
                    <Link to="/" title="">
                      <i className="fa fa-linkedin"></i>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FooterT;
