import React from "react";
import BlackGyri2 from "../../images/BlackGyri2.png";
import Headericon from "../../images/user_icon.png";
import { Link } from "react-router-dom";
import MobileMenu from "../../components/MobileMenu";

import "./style.css";

const Header = () => {
  return (
    <div className="middle-header">
      <div className="container">
        <div className="header-content">
          <div className="logo">
            <Link to="./" title="">
              <img className="imgtest" src={BlackGyri2} alt="" />
            </Link>
          </div>
          <nav className="d-lg-block d-none">
            <ul>
              <li>
                <Link to="/">Work</Link>
              </li>
              <li>
                <Link to="/service">Services</Link>
              </li>
              <li>
                <Link to="/about">About</Link>
              </li>

              <li>
                <Link to="/contact" title="">
                  Contact
                </Link>
              </li>
            </ul>
          </nav>
          <div className="contact">
            <img src={Headericon} alt="" />
            <div className="contact-info">
              <span>Needs For Help</span>
              <h4>081-3616-5990</h4>
            </div>
          </div>
          <div className="clearfix"></div>
        </div>
        <MobileMenu />
      </div>
    </div>
  );
};

export default Header;
